.main {
    min-height: 100vh;
}

/* Loading */
.loading {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bg-dark);
}

.loadingSpinner {
    width: 40px;
    height: 40px;
    border: 3px solid var(--bg-dark-tertiary);
    border-top-color: #f97316;
    border-radius: 50%;
}

/* Footer */
.footer {
    background: var(--bg-dark);
    border-top: 1px solid var(--border-subtle);
    padding: var(--space-16) var(--space-6);
}

.footerContent {
    display: flex;
    justify-content: space-between;
    gap: var(--space-12);
    max-width: 1280px;
    margin: 0 auto;
    flex-wrap: wrap;
}

.footerBrand {
    max-width: 280px;
}

.footerLogo {
    font-size: 1.375rem;
    font-weight: 800;
    font-family: var(--font-display);
    color: var(--text-primary);
}

.footerBrand p {
    margin-top: var(--space-3);
    font-size: 0.9375rem;
    color: var(--text-muted);
}

.footerLinks {
    display: flex;
    gap: var(--space-12);
    flex-wrap: wrap;
}

.footerColumn {
    display: flex;
    flex-direction: column;
    gap: var(--space-3);
}

.footerColumn h4 {
    font-size: 0.875rem;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 0 var(--space-2) 0;
}

.footerColumn a {
    font-size: 0.9375rem;
    color: var(--text-muted);
    text-decoration: none;
    transition: color var(--transition-fast);
}

.footerColumn a:hover {
    color: var(--text-primary);
}

.footerBottom {
    max-width: 1280px;
    margin: var(--space-12) auto 0;
    padding-top: var(--space-8);
    border-top: 1px solid var(--border-subtle);
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: var(--space-4);
}

.footerBottom p {
    font-size: 0.875rem;
    color: var(--text-muted);
    margin: 0;
}

/* Responsive */
@media (max-width: 768px) {
    .footerContent {
        flex-direction: column;
        gap: var(--space-8);
    }

    .footerLinks {
        gap: var(--space-8);
    }

    .footerBrand {
        max-width: none;
    }

    .footerBottom {
        flex-direction: column;
        text-align: center;
        gap: var(--space-2);
    }
}