.header {
    position: sticky;
    top: 0;
    z-index: 50;
    background: rgba(10, 10, 15, 0.9);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-bottom: 1px solid var(--border-subtle);
}

.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: var(--space-6);
    max-width: 1280px;
    margin: 0 auto;
    padding: var(--space-3) var(--space-6);
}

/* Logo */
.logo {
    display: flex;
    align-items: center;
    gap: var(--space-2);
    text-decoration: none;
}

.logoIcon {
    font-size: 1.5rem;
    color: #f97316;
}

.logoText {
    font-size: 1.375rem;
    font-weight: 800;
    font-family: var(--font-display);
    color: var(--text-primary);
}

/* Desktop Nav */
.desktopNav {
    display: flex;
    align-items: center;
    gap: var(--space-1);
}

.navLink {
    display: flex;
    align-items: center;
    gap: var(--space-1);
    padding: var(--space-2) var(--space-3);
    font-size: 0.9375rem;
    font-weight: 500;
    color: var(--text-secondary);
    border-radius: var(--radius-md);
    transition: all var(--transition-fast);
    text-decoration: none;
    background: none;
    border: none;
    cursor: pointer;
}

.navLink:hover {
    color: var(--text-primary);
    background: rgba(255, 255, 255, 0.05);
}

/* Right Section */
.rightSection {
    display: flex;
    align-items: center;
    gap: var(--space-3);
}

.searchButton {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    border: none;
    color: var(--text-secondary);
    cursor: pointer;
    transition: color var(--transition-fast);
}

.searchButton:hover {
    color: var(--text-primary);
}

.loginButton {
    font-size: 0.9375rem;
    font-weight: 500;
    color: var(--text-secondary);
    background: none;
    border: none;
    cursor: pointer;
    transition: color var(--transition-fast);
}

.loginButton:hover {
    color: var(--text-primary);
}

/* Mobile Menu Toggle */
.mobileMenuToggle {
    display: none;
    width: 40px;
    height: 40px;
    align-items: center;
    justify-content: center;
    background: transparent;
    border: none;
    color: var(--text-primary);
    cursor: pointer;
}

/* Mobile Menu */
.mobileMenu {
    overflow: hidden;
    border-top: 1px solid var(--border-subtle);
}

.mobileNav {
    display: flex;
    flex-direction: column;
    padding: var(--space-4);
    gap: var(--space-1);
}

.mobileNavLink {
    padding: var(--space-3) var(--space-4);
    font-size: 1rem;
    font-weight: 500;
    color: var(--text-secondary);
    border-radius: var(--radius-lg);
    text-decoration: none;
    transition: all var(--transition-fast);
}

.mobileNavLink:hover {
    background: rgba(255, 255, 255, 0.05);
    color: var(--text-primary);
}

.mobileActions {
    padding: var(--space-4);
    border-top: 1px solid var(--border-subtle);
}

/* Responsive */
@media (max-width: 1024px) {
    .desktopNav {
        display: none;
    }
}

@media (max-width: 768px) {
    .container {
        padding: var(--space-3) var(--space-4);
    }

    .loginButton {
        display: none;
    }

    .mobileMenuToggle {
        display: flex;
    }

    .rightSection>button:first-child {
        display: none;
    }
}